@font-face {
    font-family: "MogulShadow";
    src: url("../../../../assets/font/Mogul\ FuturisShadow.ttf")
        format("truetype");
}

.miniCarouselMainContainer {
    padding: 20px 55px;
    max-width: 1000px;
    margin: 0 auto;
    overflow: hidden;
}

.miniCarouselTitleContainer {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.miniCarouselTitle {
    width: 60%;
    min-width: 300px;
}

.miniCarouselDescriptionContainer {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.miniCarouselDescription {
    width: 70%;
    min-width: 300px;
    font-size: 18px;
    font-family: "Mogul";
}

.miniCarouselButton {
    background-color: #83d905 !important;
    border-radius: 20px !important;
    color: white !important;
    height: 30px !important;
    font-family: "Mogul";
}

.miniCarouselButton:hover {
    box-shadow: 0px 5px 10px -5px rgba(0, 0, 0, 0.75) !important;
}
