@font-face {
    font-family: "GeeksNormal";
    src: url("../../../../assets/font/GEEKS\ Bet\ \(Normal\).ttf")
        format("truetype");
}

@font-face {
    font-family: "GeeksRoman";
    src: url("../../../../assets/font/GEEKS\ OzHandicraft\ Win95BT\ \(Roman\).ttf")
        format("truetype");
}

@font-face {
    font-family: "Mogul";
    src: url("../../../../assets/font/Mogul\ Futuris.ttf") format("truetype");
}

@font-face {
    font-family: "MogulBold";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulBoldItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulShadow";
    src: url("../../../../assets/font/Mogul\ FuturisShadow.ttf")
        format("truetype");
}

.salbarMainContainer {
    padding-top: 20px;
    padding-bottom: 20px;
}

.salbarContainer {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
    background-color: #d0f6eb !important;
    width: 100% !important;
    height: 100% !important;
    box-shadow: none !important;
}

.salbarContainerTitle {
    color: #37b893;
    font-family: "MogulBold";
}

.salbarContainerPlaceContainer {
    background-color: #37b893;
    max-width: 200px;
    width: 70%;
    padding: 10px;
    border-radius: 20px;
    text-align: center;
}

.salbarContainerPlace {
    color: white;
    font-family: "MogulBold";
}
.salbarContainerFloor {
    color: #d0f6eb;
    font-family: "MogulBold";
}

.salbarTime {
    color: #37b893;
    font-family: "MogulBold";
}

.salbarButton {
    background-color: #83d905 !important;
    border-radius: 20px !important;
    color: white !important;
    height: 30px !important;
    font-family: "Mogul";
}

.salbarButton:hover {
    box-shadow: 0px 5px 10px -5px rgba(0, 0, 0, 0.75) !important;
}

.salbarContainerLeft {
    padding-right: 10px;
}

.salbarContainerMiddle {
    padding-top: 5px;
    padding-bottom: 5px;
}

.salbarContainerRight {
    padding-left: 10px;
}

.salbarMiddleCardContainer {
    min-height: 300px;
    height: 40vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.salbarMiddleLogoContainer {
    z-index: 1;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    box-shadow: 15px 15px 20px -5px rgba(0, 0, 0, 0.3) !important;
    background-color: white;
    padding-bottom: 8%;
    width: 100%;
    height: 100%;
}

.salbarMiddleLogo {
    max-width: 250px;
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 1279px) {
    .salbarMiddleLogoContainer {
        box-shadow: 0px 15px 20px -10px rgba(0, 0, 0, 0.75) !important;
    }
}
